import React from "react"
import { ContentWrapper, HeroWrapper, Overlay } from "./styled"
import { Typography } from "@mui/material"

const HeroSection: React.FC = () => {
  return (
    <HeroWrapper
      style={{
        backgroundImage:
          'url("https://images.unsplash.com/photo-1507692049790-de58290a4334?auto=format&fit=crop&w=2000&q=80")'
      }}
    >
      <Overlay />
      <ContentWrapper>
        <Typography variant="h1Bold" component={"div"}>
          Divine Directives
        </Typography>
        <Typography variant="h4Medium" component={"div"}>
          Guidelines for Spiritual Growth and Divine Connection
        </Typography>
      </ContentWrapper>
    </HeroWrapper>
  )
}

export default HeroSection
